<template>
  <div class="shipmentAdd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card :color="$store.state.colorSet.bgColor2">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" sm="3">
                <v-text-field
                  label=""
                  type="date"
                  prepend-icon="mdi-calendar-today"
                  v-model="list.date"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field
                  label=""
                  type="time"
                  prepend-icon="mdi-clock-time-eight-outline"
                  v-model="list.time"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="3">
                <v-autocomplete
                  label=""
                  prepend-icon="mdi-human-male-female"
                  :items="filterPersonnel"
                  v-model="list.personnel"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
              </v-col>
              <v-col cols="12" sm="3">
                <v-select
                  label=""
                  prepend-icon="mdi-arrow-up-down-bold"
                  :items="workTypes"
                  v-model="list.workType"
                ></v-select>
              </v-col>
              
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType == 'add'"
            ></v-btn
          >
          <v-btn class="success" @click="editOK" v-if="processType == 'edit'"
            ></v-btn
          >
        </v-card-actions>
        <pre>
          {{list}}
        </pre>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //
      processType: "", //addedit
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || ""],
      personnelItems:[], //
      workTypes:['','']
    };
  },

  computed:{
    filterPersonnel(){
      return this.personnelItems.map(i=>{
        return {
          text : i.name,
          snkey : i.snkey,
        } 
      });
    }
  },

  mounted() {
    //function (showFromParent) id
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });

    this.getData("personnel");
  },

  methods: {
    //
    getData(tableName) {
      this.myAxios("general/getAll/" + this.$store.state.databaseName + "/" + tableName
      ).then((rs) => {
        // console.log(tableName,rs)
        this[tableName + "Items"] = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          if (newItem.picname == undefined) {
            newItem.picname = "lazypic.jpg";
          }
          return newItem;
        });
      });
    },
    addProcess() {
      //,
      this.processType = "add";
      this.title = "";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        date:dayjs().format("YYYY-MM-DD")
      };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });

    },
    editProcess(item) {
      //,
      this.processType = "edit";
      this.title = "";
      this.titleStyle = "font-weight-black success lighten-2";
      this.list = { ...item }; //
    },
    addOK() {
      //
      if (this.$refs.form.validate()) {
        this.list.time += ":00";
        this.list.create_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")"; //,,
qaq
        let postData = {
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        this.myAxios(
          "general/add/" + this.$store.state.databaseName + "/timecard",
          postData
        )
          .then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "" });
              this.$emit("getAllData");
            }
          })
          .catch((err) => {
            this.$swal({ icon: "err", text: "!!:" + err });
          });
      }
    },
    editOK() {
      //
      if (this.$refs.form.validate()) {
        if (this.list.edit_man == undefined) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.name +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man; //,,
        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        this.myAxios(
          "general/edit/" + this.$store.state.databaseName + "/timecard",
          postData
        ).then((rs) => {
          if (rs.state == 1) {
            this.$swal({ icon: "success",text: "",});
            this.$emit("getAllData");
          }
        });
      }
    },
  },
};
</script>